.square {
  background: #fff;
  border: 1px solid #999;
  font-size: 24px;
  font-weight: bold;
  padding: 0;
  text-align: center; }
  .square:focus {
    outline: none; }

.square-winner {
  border: 1px solid black; }

.board {
  display: grid;
  grid-template-columns: repeat(4, 50px);
  grid-template-rows: repeat(4, 50px);
  grid-template-areas: "board-category board-col-title" "board-row-title square"; }

.board-col-title,
.board-row-title,
.board-category {
  background: #fff;
  border: 0;
  padding: 0;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center; }

.board-category {
  font-size: 12px; }

.tgl {
  display: none; }
  .tgl + .tgl-btn {
    display: block;
    margin-left: 40px;
    width: 8em;
    height: 2em;
    position: relative;
    border-width: 2px;
    border-style: outset;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
    .tgl + .tgl-btn:after, .tgl + .tgl-btn:before {
      position: relative;
      display: block;
      content: "";
      width: 50%;
      height: 100%; }
    .tgl + .tgl-btn:after {
      left: 0; }
    .tgl + .tgl-btn:before {
      display: none; }
  .tgl:checked + .tgl-btn:after {
    left: 50%; }

.tgl-skewed + .tgl-btn {
  overflow: hidden;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
  background: #888; }
  .tgl-skewed + .tgl-btn:after, .tgl-skewed + .tgl-btn:before {
    display: inline-block;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
    width: 100%;
    text-align: center;
    position: absolute;
    line-height: 2em;
    color: #fff; }
  .tgl-skewed + .tgl-btn:after {
    left: 100%;
    content: attr(data-tg-on);
    background-color: #2196F3;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#2196F3), to(#a9d4f5));
    background-image: -webkit-linear-gradient(#2196F3, #a9d4f5);
    background-image: linear-gradient(#2196F3, #a9d4f5); }
  .tgl-skewed + .tgl-btn:before {
    left: 0;
    content: attr(data-tg-off);
    background-color: #2196F3;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#a9d4f5), to(#2196F3));
    background-image: -webkit-linear-gradient(#a9d4f5, #2196F3);
    background-image: linear-gradient(#a9d4f5, #2196F3); }

.tgl-skewed:checked + .tgl-btn:before {
  left: -100%; }

.tgl-skewed:checked + .tgl-btn:after {
  left: 0; }

ol.move-list {
  display: flex;
  flex-direction: column; }
  ol.move-list.move-list-reverse {
    flex-direction: column-reverse; }

li.move-list-item {
  flex: 0 0 auto; }

