  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

.status {
  margin: 10px 0px;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game>div {
  margin: 0px 10px;
}
