.board {
  display: grid;
  grid-template-columns: repeat(4, 50px);
  grid-template-rows: repeat(4, 50px);
  grid-template-areas: "board-category board-col-title" "board-row-title square"; }

.board-col-title,
.board-row-title,
.board-category {
  background: #fff;
  border: 0;
  padding: 0;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center; }

.board-category {
  font-size: 12px; }
